#container{
    position: fixed;
    height: 100%;
    width: 100%;
    background-color: rgba(0,0,0,0.6);
    top: 0px;
    left: 0px;
    z-index: 1001;
}
#projectImage{
    height: 400px;
    width: 100%;
}
#overlayContent{
    padding-left: 20%;
    width: 60%;
}

#overlayTitle{
    float: left;
    font-size: 50px;
    font-family: -webkit-body;
    width: 100%;
    padding-top: 2%;
}

#overlayText{
    background-color: white;
    height: 500px;
}

#overlayDesc{
    font-size: 20px;
    padding-top: 10%;
}

#crossImageOverlay{
    width: 2%;
    position: absolute;
    right: 22%;
    top: 8%;
    cursor: pointer;
}